.ivd-newsletter {
  padding: 60px 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    360deg,
    rgba(255, 255, 255, 1) -30%,
    rgba(10, 81, 161, 1) 100%
  );

  &__text {
    text-align: center;
    h2 {
      margin-bottom: 30px;
    }
    a {
      margin-top: 20px;
    }
  }
  &__form-code {
    margin-top: 60px;
    .cr_page {
      width: 600px;
      max-width: 100%;
    }
    .cr_body {
      background: transparent;
    }
    .cr_ipe_item input {
      background: #fff;
      padding: 18px 30px;
      border-radius: 0px;
      font-size: 18px;
      font-family: inherit;
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);

      margin-bottom: 10px;
    }

    div[rel="checkbox"] {
      // input {
      //   width: 1.3em;
      //   height: 1.3em;
      //   background-color: white;
      //   border-radius: 50%;
      //   vertical-align: middle;
      //   border: 1px solid #ddd;
      //   -webkit-appearance: none;
      //   outline: none;
      //   cursor: pointer;
      //   &:checked {
      //     background-color: gray;
      //   }
      // }
      .dsgvo {
        font-family: inherit;
      }
      input {
        -webkit-appearance: none;
        width: 17px;
        height: 17px;
        background: white;
        border-radius: 0;
        border: none;
        display: inline-block;
        margin-bottom: 0;
        flex-shrink: 0;
        &:checked {
          background-color: #0a51a1 !important;
          &:before {
            float: left;
            display: inline-block;
            vertical-align: middle;
            width: 16px;
            line-height: 14px;
            font-family: "FontAwesome";
            font-weight: 900;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            content: "\f00c";
            margin-bottom: -1px;
            color: #13aff0;
            -moz-osx-font-smoothing: grayscale;
            color: #fff;
            font-size: 15px;
            position: relative;
            top: 2px;
            left: 1px;
          }
        }
      }
    }
    .submit_container {
      text-align: center !important;
      margin-top: 20px;
    }
    .cr_button {
      margin: 0 auto;
    }
  }
}
